import React from 'react';
import Nav from 'react-bootstrap/Nav'
import {BrowserRouter as Router, Link , Switch} from 'react-router-dom'
import Rutas from './Rutas'
import {Navbar} from 'react-bootstrap'
import Footer from './Footer'



const Navegacion = () => {
    const mystyle = {
        color: "white",
        textDecoration: "none",
      };
    //   toggleCollapse = () => {
    //     this.setState({ isOpen: !this.state.isOpen });
    //   }
    return (
        <div>
            <Router>                 
                <Navbar  className="Nav justify-content-center "expand="lg" variant="light" bg="dark">
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                {/* <Navbar.Togle onClick={this.toggleCollapse} /> */}
                        <Nav.Link  className="nav-item">
                            <Link style={mystyle} to="/" >Inicio</Link>
                        </Nav.Link>
                        <Nav.Link>
                            <Link style={mystyle} to="/Nosotros">Nosotros</Link>
                        </Nav.Link>
                        <Nav.Link>
                            <Link style={mystyle} to="/Portfolio">Portfolio</Link>
                        </Nav.Link>
                        <Nav.Link>
                            <Link style={mystyle} to="/Contacto">Contacto</Link>
                        </Nav.Link>
                </Navbar>
                        <Rutas />
            </Router>
            {/* <Footer /> */}
        </div>
    );
}
export default Navegacion;
