import React from 'react';

const Footer = () => {
    const styleGran = {
        display: 'flex',
    };

    const myStyle  = {
            width: '100%',
            textAlign: 'center',
            backgroundColor: '#151515',
            color: 'white',
            paddingTop:'8px',
            // paddingBottom:'5px',
            bottom: '0px',
          
    };

    return (
        <div style={styleGran}>
            <footer style={myStyle}>
                <p>Mza-Argentina |  Todos los derechos reservados</p>
            </footer>
        </div>
    );
}

export default Footer;