import React from 'react';
import Web from '../images/web_bg.png';
import {Image} from 'react-bootstrap/'
import Footer from '../Component/Footer'
import Tarjetas from '../Component/Tarjetas'
import {Row, Col, Container, Jumbotron, Button} from 'react-bootstrap'


    const Nosotros = () => {
        return (
            <div>
                <Row>
                    <Col xs="12" md="12" lg="12">
                        <Jumbotron>
                            <Container>
                                <Image className="img-fluid" src={Web} />

                                <h4 className="display-4">Conocenos</h4>
                                <p>
                                    This is a simple hero unit, a simple jumbotron-style component for calling
                                    extra attention to featured content or information.
                                </p>
                                <p>
                                    <Button variant="danger">Learn more</Button>
                                </p>
                            </Container>
                        </Jumbotron>
                    </Col>
                </Row>
                <Tarjetas />
                <Footer />
            </div>
        );
    }

export default Nosotros;
