import React from 'react';
import {Image, Jumbotron, Container, Row, Col} from 'react-bootstrap/'
import Logo from  '../images/logo.png'
import Portada from '../Component/Portada';
import Footer from '../Component/Footer'



    const Inicio = () => {
        const MyStyle ={
            marginRight:'0',
            marginLeft:'0',


        };

        const block ={
            width: "100%",
            height:'450px',
            backgroundColor:'#F6F7FD',
            alignItems: 'center',
            justifyContent: 'center',
            fontSize:'24px',
   

        }
        const blockB ={
            width: "100%",
            height:'450px',
            backgroundColor:'#3D4353',
            alignItems: 'center',
            justifyContent: 'center',
            color:'#ffffff',
            fontSize:'24px',

        }
        return (
            <div >   
                <Portada />
                <Jumbotron className="mt-4" bg="Dark" fluid>
                    <h1>Estamos listo para tu solución</h1>
                    <p>
                    This is a modified jumbotron that occupies the entire horizontal space of
                    its parent.
                    </p>
                </Jumbotron>
                    <Row style={MyStyle} className="mb-4" >
                        <Col className="d-flex" style={block} xs="12" md="12" lg="6">
                            <p className="align-center">No lo dudes</p>    
                        </Col>
                        <Col className="d-flex" style={blockB} xs="12" md="12" lg="6">
                            <p>Contactanos</p>  
                        </Col>
                    </Row>
                <Footer />
            </div>
        );
    }

export default Inicio;
