import React from 'react';
import {Container} from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBell, faEnvelope, faPhoneSquareAlt } from '@fortawesome/free-solid-svg-icons'

const Redes = () => {
    const myStyle = {
        display:'flex',

    };
    const myStyles = {
        marginTop:'10px',
        fontSize:'30px',
        color:'#DFDFDF'

    }
   
    return (
        <div style={myStyle}>
            <Container className="m-5">
                <div  >
                    <FontAwesomeIcon style={myStyles} icon={faPhoneSquareAlt}  />
                    <p>2615454545</p>  
                </div>
                <div>
                    <FontAwesomeIcon style={myStyles} icon={faEnvelope}  />
                    <p>njulian@gmail.com</p>  
                </div>
                <div>
                    <FontAwesomeIcon style={myStyles} icon={faBell}  />
                    <p>Lunes a Viernes 9 a 17</p>  
                </div>
              <br />
              {/* <FontAwesomeIcon icon={faBell}  /> */}

            </Container>
        </div>
    );
}

export default Redes;
