import React from 'react';
import {Form, Button,Container} from 'react-bootstrap/'

// import Form from 'react-bootstrap';

const Formulario = () => {
    const myStyle ={
        textAlign: 'left',
        margin:'0 auto',
    }
    const label={
         backgroundColor:'#dfdfdf',    
         marginBottom:'8px',
    }
    return (
        <div style={myStyle}>
            <Container>
                <Form className="m-4">
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label >Nobre completo</Form.Label>
                        <Form.Control style={label} type="text" placeholder="Nombre y apellido" />
                        <Form.Label>Email</Form.Label>
                        <Form.Control style={label} type="email" placeholder="Enter email" />
                        <Form.Label>Asunto</Form.Label>
                        <Form.Control style={label} type="email" placeholder="Enter email" />
                        <Form.Label>compañia o institución</Form.Label>
                        <Form.Control style={label} type="text" placeholder="Asunto" />
                        <Form.Group  controlId="formGridState">
                            <Form.Label>State</Form.Label>
                            <Form.Control style={label} as="select" defaultValue="Choose...">
                                <option>Otro</option>
                                <option>Presupuesto</option>
                                <option>OfertaLaboral</option>
                            </Form.Control>
                            <Form.Control style={label} className="mt-3" as="textarea" placeholder="Mensaje" rows={3} />
                        </Form.Group>
                    </Form.Group>
                    <Button variant="danger" type="submit">
                        Enviar
                    </Button>
                </Form>
            </Container>
        </div>
    );
}

export default Formulario;

