import React from 'react';
import Carousel from 'react-bootstrap/Carousel'

const Portada = () => {
    const myStyle={
        height:'100vh',
    }
    return (
        <div>
            <Carousel>
                <Carousel.Item interval={1000}>
                    <img 
                    className="img-fluid"
                    src="https://wallpaperaccess.com/full/2454628.png"
                    alt="First slide"
                    />
                    <Carousel.Caption>
                    <h3>First slide label</h3>
                    <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item interval={500}>
                    <img
                    className="img-fluid"
                    src="https://i.pinimg.com/originals/6f/2d/34/6f2d34fe6c8746c56c14fbc55308ef99.jpg"
                    alt="Second slide"
                    />
                    <Carousel.Caption>
                    <h3>Second slide label</h3>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="img-fluid"
                    src="https://i.pinimg.com/originals/15/b2/f9/15b2f9d9a09dde65c240a5318d8fe43c.jpg"
                    alt="Third slide"
                    />
                    <Carousel.Caption>
                    <h3>Third slide label</h3>
                    <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </div>
    );
}

export default Portada;
